import Decorator from '../Decorator';
import Status from '../Status';

/**
 * @class TimeoutFail
 * @extends module:jibo/bt.Decorator
 * @memberof module:jibo/bt/decorators
 * @description Forces the behavior it's decorating to fail after the specified amount of time.
 * @param {Object} options See {@link module:jibo/bt.Decorator|Decorator} for all options.
 * @param {number} options.timeout - Time in milliseconds until `TimeoutFail` forces the behavior it's decorarting to fail.
 */
export default class TimeoutFail extends Decorator {
	start() {
		this.startTime = new Date().getTime();
		return true;
	}
	update(result) {
		var elapsed = new Date().getTime() - this.startTime;
		if(elapsed > this.options.timeout) {
			this.behavior._stop();
			return Status.FAILED;
		}
		return result;
	}
}